import React, { useState, useEffect } from 'react'
import { Modal, List, InputItem, WingBlank, WhiteSpace, Button, Toast } from 'antd-mobile-v2';
import { Radio, Space } from 'antd';
import { gql, useLazyQuery } from '@apollo/client';
import { useTranslation } from 'react-i18next'

import getUserCode from '../../compoent/getUserCode';

import '../../css/Member.css'

const alert = Modal.alert;
const CHECKUSER = gql`
query  userIsExist($phone:String){
    userIsExist(phone:$phone){
 			 phone
    }
  }
`;
// 用户注册组件
export default function Member() {
    const price = {
        month: 168,
        // half: 199,
        year: 1888
    }
    let { t, i18n } = useTranslation()
    const [type, settype] = useState(1);
    const [month, setmonth] = useState(1);
    const [phone, setphone] = useState(0);
    const [devices, setdevices] = useState(1);
    const [total, settotal] = useState(0);
    const [option, setoption] = useState('');
    const [onPaying, setOnPaying] = useState(false);
    const [checkUser, { loading: loadindA, error: errorA, data: dataA }] = useLazyQuery(CHECKUSER, {
        fetchPolicy: "no-cache"
    });
    // 记录手机号和月份
    const submitPhone = (v) => {
        setphone(v);
    }
    const submitMonth = (v) => {
        setmonth(v);
    }
    const submitDevices = (v) => {
        setdevices(v);
    }
    // 付费类型
    const onChange = e => {
        console.log('选择会员', e.target.value);
        settype(e.target.value);
    };
    //计算总费用
    useEffect(() => {
        settotal(type === 1 ? month * price.month * devices : price.year * devices)
    }, [type, month, price.month, price.year, devices]);

    // 最终提交
    const submitResult = () => {
        if (phone.length === 13 && !onPaying) {
            let myreg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/;
            let phoneNum = phone.replace(/\s*/g, "");
            if (myreg.test(phoneNum)) {
                month > 0 || type !== 1 ?
                    alert(t('total') + total + t('money'), t('sure'), [
                        { text: t('wait'), onPress: () => console.log('cancel') },
                        {
                            text: t('yes'), onPress: () => {
                                let Tmonth = type === 1 ? month : 12;
                                let state = phoneNum + '_' + Tmonth + '_' + total;
                                setoption(state);
                                checkUser({
                                    variables: {
                                        "phone": '+86' + phoneNum,
                                    }
                                })
                            }
                        },
                    ]) : Toast.info(t('monthNoZero'), 1)
            }
            else {
                Toast.info(t('enterPhoneNum'), 1)
            }

        }
        else {
            onPaying ? Toast.info(t('onSendind'), 1) : Toast.info(t('enterPhoneNum'), 1)
        }
    }
    const jumptoPay = () => {
        if (phone.length === 13 && !onPaying) {
            let myreg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/;
            let phoneNum = phone.replace(/\s*/g, "");
            if (myreg.test(phoneNum)) {
                month > 0 || type !== 1 ?
                    alert(t('total') + total + t('money'), t('sure'), [
                        { text: t('wait'), onPress: () => console.log('cancel') },
                        {
                            text: t('yes'), onPress: () => {
                                let Tmonth = type === 1 ? month : 12;                           
                                let state = phoneNum + '_' + Tmonth + '_' + total;

                                setoption(state);
                                localStorage.setItem("payInfo", state);
                            
                                window.location.href = "pay.html";
                                // checkUser({
                                //     variables: {
                                //         "phone": '+86' + phoneNum,
                                //     }
                                // })
                            }
                        },
                    ]) : Toast.info(t('monthNoZero'), 1)
            }
            else {
                Toast.info(t('enterPhoneNum'), 1)
            }

        }
        else {
            onPaying ? Toast.info(t('onSendind'), 1) : Toast.info(t('enterPhoneNum'), 1)
        }

    }
    // 网络请求
    useEffect(() => {
        if (dataA) {
            setOnPaying(false);
            if (dataA.userIsExist) {
                dataA.userIsExist.phone ? getUserCode(option) : Toast.info(t('checkPhoneNo'), 2);
            }
            else {
                Toast.info(t('checkPhone'), 2);
            }

        }
        if (loadindA) {
            // console.log('检查账号中');
            setOnPaying(true);
        }
        if (errorA) {
            console.log(errorA);
            setOnPaying(false);
            Toast.info(t('netError'), 2);
        }
    }, [loadindA, errorA, dataA]);

    // 检测回车
    document.onkeydown = function (event) {
        var e = event || window.event;
        if (e && e.keyCode === 13) {
            submitResult();
        }
    };

    return (

        <div>
            <p style={{ fontSize: '30px', margin: '4vh 0 0 5vw' }}>{t('renewal')}</p>

            <WhiteSpace size="lg" />

            <WingBlank size="lg">
                <List style={{ border: '2px solid #434343', marginTop: '5vh' }}>
                    <InputItem
                        type={'phone'}
                        placeholder={t('phoneNum')}
                        clear
                        onChange={submitPhone}
                        moneyKeyboardAlign="left"
                    // moneyKeyboardWrapProps={moneyKeyboardWrapProps}
                    ></InputItem>
                </List>
            </WingBlank>

            <WhiteSpace size="lg" />

            <Radio.Group onChange={onChange} value={type} size='small' style={{ width: '90vw', margin: '10vh 0 0 5vw' }}>
                <Space direction="vertical" size={10} style={{ fontSize: '16px', width: '100%' }}>

                    <div>
                        <Radio value={1} style={{ fontSize: '16px' }}>{t('perMonth')}</Radio>
                        <List style={{ border: '2px solid #434343', width: '55px', display: 'inline-block' }}>
                            <InputItem
                                type={'bankCard'}
                                placeholder="1"
                                maxLength={2}
                                onChange={submitMonth}
                                moneyKeyboardAlign="left"
                                defaultValue={1}
                            // moneyKeyboardWrapProps={moneyKeyboardWrapProps}
                            ></InputItem>
                        </List>
                        <p className='membercost' style={{ display: 'inline-block', marginLeft: '5px' }}> {t('month')}</p>
                        <p className='membercost' style={{ float: "right", marginTop: '1.3vh' }}>{price.month}{t('money') + '/' + t('month')}</p>
                    </div>

                    {/* <div>
                        <Radio value={2} style={{ fontSize: '16px' }}>购买半年</Radio><p className='membercost' style={{ float: "right" }}>{price.half}元</p>
                    </div> */}

                    <div>
                        <p className='membercost' style={{ float: "right" }}>{price.year}{t('money')}</p><Radio value={2} style={{ fontSize: '16px' }}>{t('payOneYear')}</Radio>
                    </div>

                    <div>
                        <div value={1} style={{ display: 'inline-block', marginTop: '10px', fontSize: '16px' }}>{t('devices')}</div>
                        <p className='membercost' style={{ marginTop: '10px', display: 'inline-block', marginLeft: '8px', float: 'right' }}> {t('device')}</p>
                        <List style={{ border: '2px solid #434343', width: '55px', display: 'inline-block', float: 'right' }}>
                            <InputItem
                                type={'bankCard'}
                                placeholder="1"
                                maxLength={2}
                                onChange={submitDevices}
                                moneyKeyboardAlign="left"
                                defaultValue={1}
                            // moneyKeyboardWrapProps={moneyKeyboardWrapProps}
                            ></InputItem>
                        </List>

                    </div>

                </Space>
            </Radio.Group>

            <div className='a-line' style={{ width: '90vw', height: '2px', background: 'black', margin: '2vh 0 0 5vw' }}></div>

            <p style={{ float: "right", fontSize: '16px', margin: '2vh 5vw 0 0' }}>{total}{t('money')}</p>

            <WingBlank size="lg">
                <Button type="primary" loading={onPaying} onClick={i18n.language === 'zh' ? submitResult : jumptoPay} style={{ marginTop: '18.6vh', backgroundColor: '#434343', border: '1px solid #434343', color: '#ffffff' }}>{t('ok')}</Button>
            </WingBlank>
        </div>

    );

}