import React from 'react'
import '../../css/SignUp.css'
import { useTranslation } from 'react-i18next'
// 用户注册组件
export default function PswResult() {
    let { t, i18n } = useTranslation()
    return (

        <div>
            {i18n.language === 'zh' ?
                <div style={{ fontSize: '18px', margin: '37vh 5vw' }}>
                    <div style={{ margin: '5px 30vw' }}>{t('newPswSuccess')}</div>
                </div>
                : <div style={{ fontSize: '16px', margin: '37vh 5vw' }}>
                    <div style={{ margin: '5px 0 0 15vw' }}>{t('newPswSuccess')}</div>
                </div>}
        </div>

    );

}