import React, { useState, useEffect } from 'react'
import { Modal, WingBlank, Toast } from 'antd-mobile-v2';
import { TextArea } from 'antd-mobile';
import '../../css/SignUp.css'

const alert = Modal.alert;

// 意见反馈组件
export default function MgnUser(prop) {
    const [word, setword] = useState('');
    // const [value, setValue] = useState('')
    const submitWord = (v) => {
        setword(v)
    }
    console.log(prop);
    return (
        <div>
            <div style={{ marginTop: "25px" }} >
                <p className='mgnuser-title' style={{ paddingLeft: "15px" }}>{prop.userInfo.phone.replace('+86','')}</p>
                <p className='mgnuser-title' style={{ paddingLeft: "35px" }}>{prop.userInfo.ttl ? prop.userInfo.ttl.vipExpiredAt : '\u3000无'}</p>
                <p className='mgnuser-title' style={{ paddingLeft: "45px" }}>{prop.userInfo.deviceLimit}</p>
                <p className='mgnuser-title' style={{ paddingLeft: "20px" }}>{prop.userInfo.monthlyPayFee}</p>
                <p className='mgnuser-title' style={{ paddingLeft: "10px" }}>{prop.userInfo.totalPayFee}</p>
            </div>

            <WingBlank size="lg">
                <TextArea
                    className="mgnUser-txt"
                    style={{ border: '1px solid #434343', padding: '5px' }}
                    placeholder='备注'
                    value={word}
                    rows={2}
                    onChange={submitWord}
                />
            </WingBlank>
        </div>

    );

}