import React, { useState } from 'react'
import { List, TextareaItem, WingBlank, Button } from 'antd-mobile-v2';
import { Link } from 'react-router-dom'
import { useTranslation } from 'react-i18next'

import '../../css/SignUp.css'

// 用户注册组件
export default function AppHome(prop) {
    const [appName, setappName] = useState('');
    let { t } = useTranslation()

    const submitWord = (v) => {
        setappName(v)
    };
    const searchGame = () => {
        if (appName !== '') {
            console.log("search:" + appName);
            let jump = document.getElementById("jumpToSearch")
            jump.click();
        }
    };
    // 检测回车
    document.onkeydown = function (event) {
        var e = event || window.event;
        if (e && e.keyCode === 13) {
            searchGame();
        }
    };

    return (

        <div>

            <WingBlank size="lg">
                <Link to={{ pathname: '/tool' }}>
                    <Button type="primary"
                        style={{ marginTop: '10vh', backgroundColor: '#434343', border: '1px solid #434343', color: '#ffffff' }}>
                        {t('apps')}
                    </Button>
                </Link>
                <Link to={{ pathname: '/fitness' }}>
                    <Button type="primary"
                        style={{ marginTop: '4vh', backgroundColor: '#434343', border: '1px solid #434343', color: '#ffffff' }}>
                        {t('fitness')}
                    </Button>
                </Link>
                <Link to={{ pathname: '/education' }}>
                    <Button type="primary"
                        style={{ marginTop: '4vh', backgroundColor: '#434343', border: '1px solid #434343', color: '#ffffff' }}>
                        {t('science')}
                    </Button>
                </Link>
                <Link to={{ pathname: '/game' }}>
                    <Button type="primary"
                        style={{ marginTop: '4vh', backgroundColor: '#434343', border: '1px solid #434343', color: '#ffffff' }}>
                        {t('game')}
                    </Button>
                </Link>
                <Link to={{ pathname: '/allapp' }}>
                    <Button type="primary"
                        style={{ marginTop: '4vh', backgroundColor: '#434343', border: '1px solid #434343', color: '#ffffff' }}>
                        {t('allapps')}
                    </Button>
                </Link>

                <List style={{ border: '1px solid #434343', marginTop: '10vh' }}>
                    <List style={{ border: '2px solid #434343' }}>
                        <TextareaItem
                            rows={1}
                            count={200}
                            onChange={submitWord}
                        />
                    </List>
                </List>

                <Button type="primary"
                    onClick={searchGame}
                    style={{ marginTop: '4vh', backgroundColor: '#434343', border: '1px solid #434343', color: '#ffffff' }}>
                     {t('search')}
                </Button>
                <Link to={{ pathname: '/search', state: appName }}>
                    <button id="jumpToSearch" style={{ float: 'left', visibility: 'hidden' }}></button>
                </Link>
            </WingBlank>
        </div>

    );

}