import React from 'react'
import { WhiteSpace } from 'antd-mobile-v2';

const text = `
我们是一群致力于推动VR普及的爱好者。基于现有的VR行业发展状况，我们开发了小丑鱼VR助手。
一方面作为VR内容的分流平台让大家能更方便的玩到更多更好的VR游戏，另一方面我们也在紧密开发许多VR辅助功能，
比如云串流功能(通过云端玩STEAMVR，不再需要配置昂贵的电脑)
`
const text2 = `
小丑鱼提供的VR游戏来源于以下几个组织，在此对它们为推动VR行业普及所做的努力表达由衷的感谢。
`
// 关于我们组件
export default function AboutUs() {


    return (

        <div>
            <div style={{ fontSize: '15.5px', margin: '13vh 5vw' }}>
                <div style={{ margin: '4px 5vw' }}>关于我们</div>
                <WhiteSpace size="lg" />
                <div style={{ margin: '4px 5vw' }}>{text}</div>
                <WhiteSpace size="lg" />
                <div style={{ margin: '4px 5vw' }}>{text2}</div>
                <WhiteSpace size="lg" />
                <div style={{ margin: '4px 5vw' }}>VRMOO</div>
                <div style={{ margin: '4px 5vw' }}>STEAMUNDERGROUND</div>
            </div>

        </div>

    );

}