import React from 'react'
import { Button } from 'antd-mobile';
import '../css/paginate.css'
import { useTranslation } from 'react-i18next'

//切换页面组件
export default function Paginate(prop) {
    // const pages = Math.ceil(PageContext.stateP.total / 30)
    const pages = Math.ceil(prop.total / 15)

    let { t, i18n } = useTranslation()
    //下一页、上一页、回到首页
    function nextPage() {

        if (prop.pageNumber < pages) prop.setPage(prop.pageNumber + 1);
    }
    function lastPage() {
        if (prop.pageNumber > 1) prop.setPage(prop.pageNumber - 1);

    }
    // function toFirst() {
    //     if (prop.pageNumber > 1) prop.setPage(1);
    // }

    return (
        <div id="paginnate">
            <Button id="paginnate-last" type="text" onClick={lastPage}>{t('last')}</Button>
            <p id="paginnate-this">&nbsp;{prop.pageNumber}&nbsp; </p>
            <Button id="paginnate-next" type="text" onClick={nextPage}>{t('next')}</Button>
            <p id="paginnate-total">{t('total') + ' ' + pages + ' ' + t('page')}</p>
        </div>

    )
}