import React, { Component } from 'react';
import {
    Player,
    ControlBar,
    BigPlayButton,
    LoadingSpinner,
} from 'video-react';
import 'video-react/dist/video-react.css';
import '../css/VideoPlay.css'

export default class VideoADplay extends Component {
    constructor(props, context) {
        super(props, context);

        this.state = {

        };

        this.play = this.play.bind(this);
        this.pause = this.pause.bind(this);
        this.load = this.load.bind(this);
    }

    componentDidMount() {
        // subscribe state change
        this.player.subscribeToStateChange(this.handleStateChange.bind(this));
        this.load();

    }
    componentDidUpdate() {
        if (this.player) {
            if (this.state.player.duration === this.state.player.currentTime) {
                this.props.showX(true)
            }
        }
    }


    setMuted(muted) {
        return () => {
            this.player.muted = muted;
        };
    }

    handleStateChange(state) {
        // copy player state to this component's state
        this.setState({
            player: state
        });
    }

    play() {
        this.player.play();
        console.log(this.state.player.duration);
    }

    pause() {
        this.player.pause();
    }

    load() {
        this.player.load();
    }

    render() {
        return (
            <div id="video-outer">
                {/* <div id="ad-text">{this.state.player ? parseInt(this.state.player.duration - this.state.player.currentTime) : ''}s</div> */}
                <Player
                    ref={player => {
                        this.player = player;
                    }}
                    autoPlay
                >
                    <source src={this.props.url} />
                    <LoadingSpinner />
                    <BigPlayButton position="center" />
                    <ControlBar disableCompletely={false} />
                </Player>

            </div>
        );
    }
}