import React, { useState, useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import '../../css/SignUp.css'

// 用户注册组件
export default function PaySuccess(prop) {
    const [state, setstate] = useState({ phoneNum: 15680401073, month: 1, total: 1 });
    let { t, i18n } = useTranslation()

    useEffect(() => {
        if (prop.location.query) setstate(prop.location.query)
    }, []);

    return (

        <div>
            {i18n.language === 'zh' ?
                <div style={{ fontSize: '17px', margin: '30vh 0 0 5vw' }}>
                    <div style={{ marginLeft: 'calc(45vw - 100px)', marginTop: '5px' }}>{t('user') + state.phoneNum + "," + t('paySuccess')}</div>
                    <div style={{ marginLeft: 'calc(45vw - 80px)', marginTop: '18px' }}>{t('yourMember') + ' ' + state.month + ' ' + t('month2')}</div>
                    <div style={{ marginLeft: 'calc(45vw - 155px)', marginTop: '18px' }}>{t('feelVip')}</div>
                </div>
                :
                <div style={{ fontSize: '15px', margin: '30vh 0 0 5vw' }}>
                    <div style={{ marginLeft: 'calc(45vw - 127px)', marginTop: '5px' }}>{t('user') + state.phoneNum + "," + t('paySuccess')}</div>
                    <div style={{ marginLeft: 'calc(45vw - 137px)', marginTop: '18px' }}>{t('yourMember') + ' ' + state.month + ' ' + t('month2')}</div>
                    <div style={{ marginLeft: 'calc(45vw - 135px)', marginTop: '18px' }}>{t('feelVip')}</div>
                </div>}
        </div>

    );

}