import React, { useState, useEffect } from 'react'
import { Modal, WingBlank, WhiteSpace, Button } from 'antd-mobile-v2';
import { Radio, Space } from 'antd';
import wxpay from '../../compoent/wxpay';
// import getTicket from '../compoent/getTicket';
import axios from 'axios';
import '../../css/Member.css'

import { useTranslation } from 'react-i18next'

const alert = Modal.alert;
// 用户注册组件
export default function PayBack(prop) {
    const [type, settype] = useState(1);
    const [month, setmonth] = useState(1);
    const [total, settotal] = useState(0);
    const [phone, setphone] = useState('');
    // const [onTicket, setonTicket] = useState(false);
    const [onPaying, setOnPaying] = useState(false);

    let { t } = useTranslation()

    function getQueryVariable(variable) {
        var query = window.location.search.substring(1);
        // var query = window.location.hash.substring(11);
        var vars = query.split("&");
        for (var i = 0; i < vars.length; i++) {
            var pair = vars[i].split("=");
            if (pair[0] === variable) { return pair[1]; }
        }
        return (false);
    }

    const submitResult = () => {
        let code = getQueryVariable("code");
        let state = getQueryVariable("state");
        let opt = {
            phoneNum: state.split('_')[0],
            month: state.split('_')[1],
            total: state.split('_')[2],
        }
        if (!onPaying) {
            if (state && code) {
                // if (window.apiticket) {
                setOnPaying(true)
                wxpay(axios, code, opt, setOnPaying, prop)
                // }else alert('微信服务调用失败，请返回重新尝试');

            }
            else {
                alert(t('payInfoErr'))
            }
        }


    }

    useEffect(() => {
        // console.log('跳转成功');
        // getTicket(axios, setonTicket);
        let state = getQueryVariable("state");
        if (state) {
            let phoneNum = state.split('_')[0];
            let month = state.split('_')[1];
            let total = state.split('_')[2];

            setmonth(month)
            setphone(phoneNum)
            settotal(total)
        }
        else {
            settype(false)
        }

    }, []);

    return (

        <div>
            {type ?
                <div>
                    <p style={{ fontSize: '30px', margin: '4vh 0 0 5vw' }}>{t('checkOrder')}</p>

                    <WhiteSpace size="lg" />
                    <div style={{ fontSize: '25px', margin: '2vh 0 0 5vw' }}>{t('phone')}: {phone}</div>

                    <Radio.Group value={1} size='small' style={{ width: '90vw', margin: '10vh 0 0 5vw' }}>

                        <Space direction="vertical" size={10} style={{ fontSize: '16px', width: '100%' }}>
                            <div>
                                <Radio value={1} style={{ fontSize: '16px' }}>{t('totalpay')}</Radio><p className='membercost' style={{ float: "right" }}>{month + t('month2')}</p>
                            </div>
                        </Space>
                    </Radio.Group>

                    <div className='a-line' style={{ width: '90vw', height: '2px', background: 'black', margin: '2vh 0 0 5vw' }}></div>

                    <p style={{ float: "right", fontSize: '16px', margin: '2vh 5vw 0 0' }}>{t('total') + total + t('money')}</p>

                    <WingBlank size="lg">

                        <Button type="primary" loading={onPaying} onClick={submitResult} style={{ marginTop: '18.6vh', backgroundColor: '#434343', border: '1px solid #434343', color: '#ffffff' }}>{onPaying ? t('paying') : t('confirmPay')}</Button>

                    </WingBlank>
                </div>
                : ''
            }

        </div>

    );

}