module.exports = function (axios, openid, userip, opt, cb) {
    console.log('开始下单');
    let option = {
        phone: '+86' + opt.phoneNum,
        month: opt.month
    }
    let optString = JSON.stringify(option)
    let xmlParse = function (xmla) {
        let parser = new DOMParser();
        let xmlDoc = parser.parseFromString(xmla, "text/xml");
        console.log(xmlDoc);
        let return_code = xmlDoc.getElementsByTagName('return_code')[0].textContent;
        let return_msg = xmlDoc.getElementsByTagName('return_msg')[0].textContent;
        if (return_code === 'FAIL') {
            console.log(return_msg);
            return false
        }
        else {
            let result_code = xmlDoc.getElementsByTagName('result_code')[0].textContent;
            let prepay_id = xmlDoc.getElementsByTagName('prepay_id')[0].textContent;
            var dataObj = {
                return_code: return_code,
                return_msg: return_msg,
                result_code: result_code,
                prepay_id: prepay_id,
            }
            return dataObj;
        }

    };

    axios({
        // url: 'https://wechat.bumoyu.com/api/wechat/createpay',
        url: 'https://wechatdev.bumoyu.com/api/wechat/createpay',
        method: 'post',
        headers: {
            'accept': 'application/json',
            'Content-Type': 'application/json'
        },
        data: {
            "openid": `${openid}`,
            "userIP": `${userip}`,
            "total_fee": `${opt.total*100}`,
            "attach": optString
        },
    }).then(v => {
        console.log(v.data);
        let prepayID = xmlParse(v.data);
        if (prepayID) {
            console.log('下单完毕，payID：' + prepayID);
            cb(prepayID.prepay_id)
        }

    }).catch(function (errors) {
        // console.log('wxPayStart errors', errors);
        console.log(errors);
    });

}