import React from 'react'
import { Link } from 'react-router-dom'
import '../css/gameCard.css'


// 用户注册组件
export default function gameSmallCard(prop) {

    const gameStart = (v) => {
        // prop.history.push({pathname:'/detail',query:prop.gameInfo});
        document.getElementsByClassName("jumpToDetail")[prop.testkey].click();
        console.log(prop);
    };

    return (

        <div className='gameScard-outer'>
            <div className='gameScard-word'  onClick={gameStart}>
                {prop.gameInfo.name}
            </div>
            <img className='gameScard-pic' src={prop.gameInfo.cover} alt='网络错误' onClick={gameStart} />
            <Link to={{ pathname: '/detail', state: prop.gameInfo }}>
                <button className="jumpToDetail" style={{ float: 'left', display: 'none' }}></button>
            </Link>
        </div>

    );

}