import React, { useState, useEffect } from 'react'
import { List, InputItem, WingBlank, WhiteSpace, Button, Toast, Flex, } from 'antd-mobile-v2';
import { gql, useMutation, useLazyQuery } from '@apollo/client';
import { useTranslation } from 'react-i18next'

import '../../css/SignUp.css'

const USER_SIGNUP = gql`
mutation signup($signUpInput:SignUpInput!){
    signup(signUpInput:$signUpInput){
    id
    phone
    ttl{
      vipType
      vipExpiredAt
    }
    token
    }
  }
`;
const SENDSMS = gql`
mutation createSMS($createSMSInput:CreateSMSInput!){
    createSMS(createSMSInput:$createSMSInput){
    id
    expiredAt
    createdAt
    updatedAt
    }
  }
`;
const CHECKUSER = gql`
query  userIsExist($phone:String){
    userIsExist(phone:$phone){
 			 phone
    }
  }
`;
// 用户注册组件
export default function SignUp(prop) {
    const [phone, setphone] = useState(0);
    const [mgnID, setmgnID] = useState(0);
    const [hasError, sethasError] = useState(false);
    const [firstPsw, setfirstPsw] = useState('');
    const [secondPsw, setsecondPsw] = useState('');
    const [onSigning, setonSigning] = useState(false);
    const [onCodeing, setonCodeing] = useState(false);
    const [whenSendCode, setwhenSendCode] = useState(false);
    const [codeTime, setcodeTime] = useState(60);
    const [code, setcode] = useState('');
    const [codeID, setcodeID] = useState('');
    let { t, i18n } = useTranslation()

    const [signUp, { loading: loadingA, error: errorA, data: dataA }] = useMutation(USER_SIGNUP, {
        fetchPolicy: "no-cache"
    });
    const [sendSMS, { loading: loadindB, error: errorB, data: dataB }] = useMutation(SENDSMS, {
        fetchPolicy: "no-cache"
    });
    const [checkUser, { loading: loadindC, error: errorC, data: dataC }] = useLazyQuery(CHECKUSER, {
        fetchPolicy: "no-cache"
    });

    //输入内容
    const submitPhone = (v) => {
        let phoneNum = v.replace(/\s*/g, "");
        setphone(phoneNum);
    }
    const submitPsw = (v) => {
        console.log(v);
        setfirstPsw(v);
        if (v !== secondPsw) {
            sethasError(true);
        }
        else {
            sethasError(false);
        }
    }
    const submitMgn = (v) => {
        setmgnID(v);
    }
    const confirmPsw = (v) => {
        setsecondPsw(v);
        if (v !== firstPsw) {
            sethasError(true);
        }
        else {
            sethasError(false);
        }
    }
    // 校验密码
    const onErrorClick = () => {
        if (hasError) {
            Toast.info(t('pswCheck'), 1); { }
        }
    }

    // 提交注册
    const submitResult = () => {
        if (onSigning) {
            Toast.info(t('signingWait'), 1.5);
        }
        else {
            if (phone.length === 11) {
                let myreg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/;
                if (!myreg.test(phone)) {
                    Toast.info(t('enterPhoneNum'), 1);
                }
                else if (hasError) {
                    Toast.info(t('enterPsw'), 1.5);
                }
                else if (firstPsw.length < 6) {
                    Toast.info(t('pswAtLeast'), 1.5);
                }
                else if (code.length !== 6 || codeID === '') {
                    Toast.info(t('verifyCodeOverdue'), 1.5);
                }
                else {
                    console.log({ phone: '+86' + phone, psw: firstPsw, verifyCodeId: codeID, verifyCode: code });
                    signUp({
                        variables: {
                            signUpInput: {
                                "password": firstPsw,
                                "phone": '+86' + phone,
                                "verifyCodeId": codeID,
                                "verifyCode": code,
                                "salerName": mgnID
                            }
                        }
                    })

                }


            }
            else {
                Toast.info(t('enterPhoneNum'), 1);
            }
        }

    }
    const setCode = (v) => {
        setcode(v);
    }

    // 发送验证开始检测
    const submitVerify = () => {
        if (!onCodeing) {
            let myreg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/;
            if (myreg.test(phone)) {
                checkUser({
                    variables: {
                        "phone": '+86' + phone,
                    }
                })
                setonCodeing(true);
            }
            else {
                Toast.info(t('enterPhoneNum'), 1.5);
            }
        }
        else {
            Toast.info(t('onSendind'), 1);
        }
    }

    //验证码间隔倒计时
    useEffect(() => {
        if (whenSendCode) {
            let time = 60;
            var aa = setInterval(function () {
                if (time > 0) {
                    time -= 1;
                    setcodeTime(time);
                }
                else {
                    setcodeTime(60);
                    setwhenSendCode(false);
                    clearInterval(aa);
                }
            }, 1000);
        }
    }, [whenSendCode]);

    //注册账号
    useEffect(() => {
        if (dataA) {
            setonSigning(false);
            prop.history.push({ pathname: '/signSuccess', query: { phone: phone } });
        }
        if (loadingA) {
            setonSigning(true);
            // console.log('注册中');
        }
        if (errorA) {
            setonSigning(false);
            if (errorA.graphQLErrors.length > 0) {
                console.log(errorA.graphQLErrors);
                if (errorA.graphQLErrors[0].extensions) {
                    let res = errorA.graphQLErrors[0].extensions.response;
                    console.log(res);
                    for (let i = 0; i < res.length; i++) {
                        if (res[i].search('already exist') > -1) {
                            Toast.info(t('phoneAlreadyHave'), 1.5);

                        }
                        else if (res[i].search('verifyCode') > -1) {
                            Toast.info(t('verifyCodeErr'), 1.5);
                        }
                        else {
                            Toast.info(t('verifyCodeErrRetry'), 1.5);
                        }
                    }
                }
                else {
                    Toast.info(t('netError'), 1.5);
                    console.log(errorA.graphQLErrors);
                }
            }
            else {
                Toast.info(t('netError'), 1.5);
            }
        }
    }, [loadingA, dataA, errorA]);

    //发送验证码
    useEffect(() => {
        if (dataB) {
            Toast.info('验证码已发送', 1);
            setcodeID(dataB.createSMS.id);
        }
        if (loadindB) {
            console.log('验证码发送中');
        }
        if (errorB) {
            if (errorB.graphQLErrors.length > 0) {
                if (errorB.graphQLErrors[0].extensions) {
                    let res = errorB.graphQLErrors[0].extensions.response;
                    console.log(res);
                    for (let i = 0; i < res.length; i++) {
                        if (res[i].search('verifyCode') > -1) {
                            Toast.info('验证码填写错误', 1.5);
                        }
                        else {
                            Toast.info('验证码错误，请重新发送', 1.5);
                        }
                    }
                }
                else {
                    Toast.info('网络错误，提交失败', 1.5);
                    console.log(errorB.graphQLErrors);
                }
            }
            else {
                Toast.info('网络错误，提交失败', 1.5);
                console.log(errorB);
            }
        }
    }, [loadindB, errorB, dataB]);

    // 检测用户
    useEffect(() => {
        if (dataC) {
            setonCodeing(false);
            if (dataC.userIsExist) {
                if (dataC.userIsExist.phone) Toast.info(t('phoneAlreadyHave'), 2);
                else {
                    setwhenSendCode(true);
                    sendSMS({
                        variables: {
                            createSMSInput: {
                                "phone": '+86' + phone
                            }
                        }
                    });
                }

            }
            else {
                Toast.info(t('checkPhone'), 2);
            }

        }
        if (loadindC) {
            // console.log('检查账号中');
        }
        if (errorC) {
            console.log(errorC);
            setonCodeing(false);
            Toast.info(t('netError'), 2);
        }
    }, [loadindC, errorC, dataC]);

    document.onkeydown = function (event) {
        var e = event || window.event || arguments.callee.caller.arguments[0];
        if (e && e.keyCode === 13) {
            submitResult();
        }
    };

    return (

        <div>
            <p style={{ fontSize: '30px', margin: '4vh 0 5vw 5vw', display: 'inline-block' }}>{t('signUp')}</p>
            <p style={{ fontSize: '15px', margin: '4vh 0 5vw 5vw', display: 'inline-block' }}>{t('present')}</p>

            <WhiteSpace size="lg" />

            <WingBlank size="lg">
                <List style={{ border: '2px solid #434343' }}>
                    <InputItem
                        type={'phone'}
                        placeholder={t('phoneNum')}
                        clear
                        onChange={submitPhone}
                        moneyKeyboardAlign="left"
                    // moneyKeyboardWrapProps={moneyKeyboardWrapProps}
                    ></InputItem>
                </List>
                <WhiteSpace size="lg" />
                <List style={{ border: '2px solid #434343' }}>
                    <InputItem
                        type={'password'}
                        placeholder={t('password')}
                        clear
                        onChange={submitPsw}
                        moneyKeyboardAlign="left"
                    // value={firstPsw}
                    // moneyKeyboardWrapProps={moneyKeyboardWrapProps}
                    ></InputItem>

                </List>
                <WhiteSpace size="lg" />
                <List style={{ border: '2px solid #434343' }}>
                    <InputItem
                        type={'password'}
                        placeholder={t('checkPSW')}
                        clear
                        onChange={confirmPsw}
                        moneyKeyboardAlign="left"
                        error={hasError}
                        onErrorClick={onErrorClick}
                    // moneyKeyboardWrapProps={moneyKeyboardWrapProps}
                    ></InputItem>
                </List>
                <WhiteSpace size="lg" />
                {i18n.language === 'zh' ?
                    <List style={{ border: '2px solid #434343' }}>
                        <InputItem
                            type={'text'}
                            placeholder={t('manegerID')}
                            clear
                            onChange={submitMgn}
                            moneyKeyboardAlign="left"
                        // moneyKeyboardWrapProps={moneyKeyboardWrapProps}
                        ></InputItem>
                    </List>
                    : ''}
                <WhiteSpace size="lg" />
                <Flex>
                    <Flex.Item>
                        <List style={{ border: '2px solid #434343', width: '45vw', float: 'left' }}>
                            <InputItem
                                type={'text'}
                                placeholder={t('verifyCode')}
                                clear
                                onChange={setCode}
                                moneyKeyboardAlign="left"
                            // moneyKeyboardWrapProps={moneyKeyboardWrapProps}
                            ></InputItem>

                        </List>
                    </Flex.Item>
                    <Flex.Item>
                        <Button type="primary"
                            onClick={submitVerify}
                            style={{ width: '45vw', float: 'right', backgroundColor: '#434343', border: '1px solid #434343', color: '#ffffff' }}
                            disabled={whenSendCode}
                            loading={onCodeing}
                        >{t('sendCode')}{whenSendCode ? codeTime + 's' : ''}</Button>
                    </Flex.Item>
                </Flex>
            </WingBlank>



            <WingBlank size="lg">
                <Button type="primary"
                    loading={onSigning}
                    onClick={submitResult}
                    style={{ marginTop: '13vh', backgroundColor: '#434343', border: '1px solid #434343', color: '#ffffff' }}>
                    {onSigning ? t('signing') : t('ok')}
                </Button>
            </WingBlank>
        </div>

    );

}