import React, { useState, useEffect } from 'react'
import { Modal, List, WingBlank, WhiteSpace, Button, TextareaItem, Toast } from 'antd-mobile-v2';
import { gql, useMutation } from '@apollo/client';
import '../../css/SignUp.css'

const alert = Modal.alert;
const FEEDBACK = gql`
mutation createFeedback($createFeedbackInput:CreateFeedbackInput!){
    createFeedback(createFeedbackInput:$createFeedbackInput){
    id
    content
    version
    contact
    createdAt
    updatedAt
    }
  }
`;
// 意见反馈组件
export default function Feedback(prop) {
    const [word, setword] = useState('');
    const [user, setuser] = useState('');
    const [sending, setsending] = useState('');
    const [feedback, { loading: loadingA, error: errorA, data: dataA }] = useMutation(FEEDBACK);

    const submitUser = (v) => {
        setuser(v)
    }
    const submitWord = (v) => {
        setword(v)
    }


    const submitResult = () => {

        if (word === '') {
            alert('请输入内容');
        }
        else {
            alert('确认提交吗', '', [
                { text: '取消', onPress: () => console.log('cancel bugsub') },
                {
                    text: '是的', onPress: () => {
                        let contact = user === "" ? 'unknown' : user
                        console.log(contact, word);
                        feedback({
                            variables: {
                                createFeedbackInput: {
                                    "version": "FeedBack",
                                    "contact": contact,
                                    "content": word
                                }
                            }
                        })

                    }
                },
            ])
        }

    }

    useEffect(() => {
        if (dataA) {
            setsending(false)
            console.log(dataA);
            prop.history.push({ pathname: '/backOver2' });
        }
        if (loadingA) {
            setsending(true)
        }
        if (errorA) {
            setsending(false);
            Toast.info('网络错误，提交失败', 2);
            if (errorA.graphQLErrors) {
                console.log(errorA.graphQLErrors);
            }
            else {
                console.log(errorA);
            }
        }
    }, [errorA, dataA, loadingA]);

    return (

        <div>
            <p style={{ fontSize: '30px', margin: '4vh 0 0 5vw' }}>意见反馈</p>

            <WhiteSpace size="lg" />
            <WhiteSpace size="lg" />

            <WingBlank size="lg">
                <List style={{ border: '2px solid #434343' }}>
                    <TextareaItem
                        rows={7}
                        count={200}
                        onChange={submitWord}
                    />
                </List>
                <WhiteSpace size="lg" />
                <WhiteSpace size="lg" />
                <WhiteSpace size="lg" />

                <List style={{ border: '2px solid #434343' }}>
                    <TextareaItem
                        rows={1}
                        count={18}
                        onChange={submitUser}
                        placeholder="手机号，为空则匿名"
                    />
                    {/* <InputItem
                        type={'text'}
                        placeholder="手机号，为空则匿名"
                        maxLength={20}
                        clear
                        onChange={submitUser}
                        moneyKeyboardAlign="left"
                    // moneyKeyboardWrapProps={moneyKeyboardWrapProps}
                    ></InputItem> */}

                </List>

                <WhiteSpace size="lg" />
            </WingBlank>

            <WingBlank size="lg">
                <Button type="primary" loading={sending} onClick={submitResult} style={{ marginTop: '10vh', backgroundColor: '#434343', border: '1px solid #434343', color: '#ffffff' }}>确&nbsp;&emsp;定</Button>
            </WingBlank>
        </div>

    );

}