import React, { useState, useEffect } from 'react'
import { gql, useLazyQuery } from '@apollo/client';
import { WingBlank, Button } from 'antd-mobile-v2';
import GameSmallCard from '../../compoent/gameSmallCard';
import { Link } from 'react-router-dom';
import { useTranslation } from 'react-i18next'

import Paginate from '../../compoent/paginate';

import '../../css/SignUp.css'

const GET_GAMEINFO = gql`
query apps($paginatedAppInput:PaginatedAppInput!){
    apps(paginatedAppInput:$paginatedAppInput){
      totalCount
    lastKey{
      id
      field
      value
    }
	edges{
        node{
            id
            updatedAt
            video
            name
            cover
            description
            rating
        }
      }
    }
  }
`;
// 用户注册组件
export default function SearchPage(prop) {
    let { t } = useTranslation()

    const [PageInfo, setPageInfo] = useState([]);
    const [PageNum, setPageNum] = useState(1);
    const [getfilter, { loading, error, data }] = useLazyQuery(GET_GAMEINFO);

    const getPageInfo = (v) => {
        let arr = PageInfo;
        arr.push(v);
        setPageInfo(arr);
    }

    useEffect(() => {
        if (PageNum === 1) {
            console.log('初始化分页加载');
            getfilter({
                variables: {
                    paginatedAppInput: {
                        "limit": 15,
                        "order": "descending",
                        "orderBy": "createdAt",
                        "filters": {
                            "condition": "contains",
                            "attribute": "name",
                            "value": prop.location.state
                        }
                    }
                }
            })
        }
        else {
            getfilter({
                variables: {
                    paginatedAppInput: {
                        "limit": 15,
                        "lastKey": {
                            "id": PageInfo[PageNum - 2].id,
                            "field": PageInfo[PageNum - 2].field,
                            "value": PageInfo[PageNum - 2].value
                        },
                        "order": "descending",
                        "orderBy": "createdAt",
                        "filters": {
                            "condition": "contains",
                            "attribute": "name",
                            "value": prop.location.state
                        }
                    }
                }
            })
        }

    }, [PageNum]);

    useEffect(() => {
        if (data) {
            getPageInfo(data.apps.lastKey)
        }
    }, [data]);


    if (loading) {
        return <div className="pageOnloading">
            {t('loading')}...
        </div >
    }

    if (error) {
        let err = error.message
        console.log(err.search("Received status code 400"))
        console.log(error);
        return <div className="pageOnerror">
            {t('loadingErr')}
        </div >
    }
    if (data) {
        return <div className="pageview">
            {/* <div style={{ height: '55px' }}> */}
            <WingBlank size="lg">
                <Link to={{ pathname: '/app' }}>
                    <Button type="primary"
                        className="cardBackButton">
                        {t('backToHome')}
                    </Button>
                </Link>
                <div className="cardBackWord"> {t('search')}：{prop.location.state}</div>
            </WingBlank>
            {/* </div> */}
            {
                data.apps.edges.length > 0 ?
                    data.apps.edges.map((ele, ind) => {
                        return <GameSmallCard key={ind} testkey={ind} gameInfo={ele.node} />
                    })
                    :
                    <div style={{ fontSize: '4vh', margin: '25vh 0 0 32vw' }}>未搜索到结果</div>
            }
            <Paginate setPage={setPageNum} pageNumber={PageNum} total={data.apps.totalCount} />
        </div >

    }
    return (
        <div>
        </div>
    );

}