import React from 'react'
import { WingBlank, Button } from 'antd-mobile-v2';
import '../../css/SignUp.css'
import { useTranslation } from 'react-i18next'

// 用户注册组件
export default function PayFailed() {
    // 通过自定义 moneyKeyboardWrapProps 修复虚拟键盘滚动穿透问题
    // const isIPhone = new RegExp('\\biPhone\\b|\\biPod\\b', 'i').test(window.navigator.userAgent);
    // let moneyKeyboardWrapProps;
    // if (isIPhone) {
    //     moneyKeyboardWrapProps = {
    //         onTouchStart: e => e.preventDefault(),
    //     };
    // }
    let { t, i18n } = useTranslation()

    const submitResult = () => {
        window.opener = null;
        window.open('', '_self');
        window.close();
    }

    return (

        <div>
            {i18n.language === 'zh' ?
                <div style={{ fontSize: '18px', margin: '32.4vh 5vw' }}>
                    <div style={{ marginLeft: 'calc(45vw - 107px)' }}>{t('payFailed')}</div>
                    <div style={{ marginLeft: 'calc(45vw - 90px)' }}>{t('service')}</div>
                </div>
                : <div style={{ fontSize: '16px', margin: '32.4vh 5vw' }}>
                    <div style={{ marginLeft: 'calc(45vw - 125px)' }}>{t('payFailed')}</div>
                    <div style={{ marginLeft: 'calc(45vw - 140px)' }}>{t('service')}</div>
                </div>}


            <WingBlank size="lg">
                <Button type="primary" onClick={submitResult} style={{ marginTop: '10vh', backgroundColor: '#434343', border: '1px solid #434343', color: '#ffffff' }}>{t('ok')}</Button>
            </WingBlank>
        </div>

    );
}