import React, { useState, useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import '../../css/SignUp.css'

// 用户注册组件
export default function SignSuccess(prop) {
    const [state, setstate] = useState('');
    let { t } = useTranslation()

    useEffect(() => {
        setstate(prop.location.query)
    }, []);

    return (

        <div>
            <div style={{ fontSize: '17px', margin: '35vh 0 0 5vw' }}>
                <div style={{ marginLeft: 'calc(45vw - 105px)', marginTop: '5px' }}>{t('verifyCodeOverdue')} {state.phone} {t('loginSuccessful')}</div>
                <div style={{ marginLeft: 'calc(45vw - 93px)', marginTop: '18px' }}>{t('loginNow')}</div>
            </div>
        </div>

    );

}