module.exports = function (axios, code, cbSuccess,cbFailed) {
    console.log('开始获取openid');
    axios({
        // url: 'https://wechat.bumoyu.com/api/wechat/openid/token',
        url: 'https://wechatdev.bumoyu.com/api/wechat/openid/token',
        method: 'post',
        headers: {
            'accept': 'application/json',
            'Content-Type': 'application/json'
        },
        data: {
            "code": `${code}`
        },
    }).then(v => {
        let openid = v.data.openid;
        console.log('获取openid:' + openid);
        cbSuccess(axios, openid)
    }).catch(function (errors) {
        alert('登录信息过期，请返回重新尝试');
        cbFailed(false)
        console.log(errors);
    });

}