import React, { useState } from 'react'
import { WingBlank, Button } from 'antd-mobile-v2';
import { Mask, Rate } from 'antd-mobile';
import { Link } from 'react-router-dom'
import VideoPlay from '../../compoent/VideoPlay'
import { useTranslation } from 'react-i18next'

import '../../css/DetailPage.css'


// 用户注册组件
export default function DetailPage(prop) {
    const [visible, setVisible] = useState(false);
    let { t } = useTranslation()
    
    const showMask = () => {
        setVisible(true);
    }
    // useEffect(() => {
    //     setInfo(prop.location.state)
    // }, []);

    return (

        <div className='detail-outer'>
            <p className='detail-word'>{prop.location.state.name}</p>
            <img className='detail-pic' src={prop.location.state.cover} alt='网络错误' />
            <Rate style={{ margin: '1vh 0 0 7vw', }} readOnly value={prop.location.state.rating} />
            <div className='detail-des'>
                {prop.location.state.description}
            </div>
            <div>
                <Mask destroyOnClose={true} visible={visible} opacity={0.9} onMaskClick={() => setVisible(false)}>
                    <VideoPlay url={prop.location.state.video} />
                </Mask>
                <WingBlank size="lg">
                    <Button type="primary"
                        onClick={showMask}
                        style={{ marginTop: '10vh', backgroundColor: '#434343', border: '1px solid #434343', color: '#ffffff' }}>
                        {t('play')}
                    </Button>
                    <Link to={{ pathname: '/app' }}>
                        <Button type="primary"
                        style={{ marginTop: '2vh', backgroundColor: '#434343', border: '1px solid #434343', color: '#ffffff' }}>
                            {t('backToHome')}
                        </Button>
                    </Link>
                </WingBlank>
            </div>

        </div>


    );

}