
import React, { useState, useEffect } from 'react'
import { List, InputItem, WingBlank, WhiteSpace, Button, Toast, Flex, } from 'antd-mobile-v2';
import { gql, useMutation, useLazyQuery } from '@apollo/client';

import '../../css/SignUp.css'

const USER_SIGNUP = gql`
mutation salerLogin($salerLoginInput:SalerLoginInput!){
    salerLogin(salerLoginInput:$salerLoginInput){
    name
    id
    token
    }
  }
`;
// 用户注册组件
export default function MgnLogin(prop) {
    const [phone, setphone] = useState(0);
    const [firstPsw, setfirstPsw] = useState('');
    const [onSigning, setonSigning] = useState(false);

    const [signUp, { loading: loadingA, error: errorA, data: dataA }] = useMutation(USER_SIGNUP, {
        fetchPolicy: "no-cache"
    });
    const submitPhone = (v) => {
        let phoneNum = v.replace(/\s*/g, "");
        setphone(phoneNum);
    }
    const submitPsw = (v) => {
        setfirstPsw(v);
    }

    // 提交注册
    const submitResult = () => {
        if (onSigning) {
            Toast.info('正在注册中，请稍等', 1.5);
        }
        else {
            signUp({
                variables: {
                    salerLoginInput: {
                        "password": firstPsw,
                        "name": phone
                    }
                }
            })
            // if (phone.length === 11) {
            //     let myreg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/;
            //     if (!myreg.test(phone)) {
            //         Toast.info('请输入正确的手机号', 1);
            //     }
            //     else if (firstPsw.length < 6) {
            //         Toast.info('密码需要至少6位', 1.5);
            //     }
            //     else {
            //         console.log({ phone: '+86' + phone, psw: firstPsw });
            //         prop.history.push({ pathname: '/mgnList', query: { phone: phone, psw: firstPsw ,monthlySales:10000,totalSales:90000} });


            //     }


            // }
            // else {
            //     Toast.info('请输入正确的手机号', 1);
            // }
        }

    }
    const getManagerInfo = () => {
        console.log('成为客户经理只需要去当客户经理就行啦');
    }

    //注册账号
    useEffect(() => {
        if (dataA) {
            if(dataA.salerLogin.token) {
                setonSigning(false);
                prop.history.push({pathname:'/mgnList',query:dataA.salerLogin.name});
            }

        }
        if (loadingA) {
            setonSigning(true);
        }
        if (errorA) {
            setonSigning(false);
            if (errorA.graphQLErrors.length > 0) {
                console.log(errorA.graphQLErrors);
                // if (errorA.graphQLErrors[0].extensions) {
                //     let res = errorA.graphQLErrors[0].extensions.response;
                //     console.log(res);
                //     for (let i = 0; i < res.length; i++) {
                //         if (res[i].search('already exist') > -1) {
                //             Toast.info('该手机号已注册', 1.5);
                //         }
                //         else if (res[i].search('verifyCode') > -1) {
                //             Toast.info('验证码填写错误', 1.5);
                //         }
                //         else {
                //             Toast.info('验证码错误，请重新发送', 1.5);
                //         }
                //     }
                // }
                // else {
                //     Toast.info('网络错误', 1.5);
                //     console.log(errorA.graphQLErrors);
                // }
            }
            else {
                console.log(errorA);
                Toast.info('网络错误', 1.5);
            }
        }
    }, [loadingA, dataA, errorA]);


    // document.onkeydown = function (event) {
    //     var e = event || window.event || arguments.callee.caller.arguments[0];
    //     if (e && e.keyCode === 13) {
    //         submitResult();
    //     }
    // };

    return (

        <div>
            <p style={{ fontSize: '30px', margin: '4vh 0 5vw 5vw' }}>客户经理登录</p>

            <WhiteSpace size="lg" />

            <WingBlank size="lg">
                <List style={{ border: '2px solid #434343' }}>
                    <InputItem
                        type={'text'}
                        placeholder="客户经理账号"
                        clear
                        onChange={submitPhone}
                        moneyKeyboardAlign="left"
                    ></InputItem>
                </List>
                <WhiteSpace size="lg" />
                <List style={{ border: '2px solid #434343' }}>
                    <InputItem
                        type={'password'}
                        placeholder="密码"
                        clear
                        onChange={submitPsw}
                        moneyKeyboardAlign="left"
                    ></InputItem>

                </List>
            </WingBlank>



            <WingBlank size="lg">
                <Button type="primary"
                    loading={onSigning}
                    onClick={submitResult}
                    style={{ marginTop: '13vh', backgroundColor: '#434343', border: '1px solid #434343', color: '#ffffff' }}>
                    {onSigning ? "登 录 中" : "登\u3000录"}
                </Button>
                <Button type="primary"
                    loading={onSigning}
                    onClick={getManagerInfo}
                    style={{ marginTop: '3vh', backgroundColor: '#434343', border: '1px solid #434343', color: '#ffffff' }}>
                    成为客户经理
                </Button>
            </WingBlank>
        </div>

    );

}