import React, { useState, useEffect } from 'react'
import { List, InputItem, WingBlank, WhiteSpace, Button, Flex, Toast } from 'antd-mobile-v2';
import { gql, useMutation, useLazyQuery } from '@apollo/client';
import { useTranslation } from 'react-i18next'

import '../../css/SignUp.css'

const FINDPASS = gql`
mutation findpass($findPassInput:FindPassInput!){
    findpass(findPassInput:$findPassInput){
    success
    error
  }
  }
`;
const SENDSMS = gql`
mutation createSMS($createSMSInput:CreateSMSInput!){
    createSMS(createSMSInput:$createSMSInput){
    id
    expiredAt
    createdAt
    updatedAt
    }
  }
`;
const CHECKUSER = gql`
query  userIsExist($phone:String){
    userIsExist(phone:$phone){
 			 phone
    }
  }
`;
// 找回密码组件
export default function FindPsw(prop) {
    const [phone, setphone] = useState(0);
    const [code, setcode] = useState('');
    const [firstPsw, setfirstPsw] = useState('');
    const [secondPsw, setsecondPsw] = useState('');
    const [hasError, sethasError] = useState(false);
    const [whenSendCode, setwhenSendCode] = useState(false);
    const [onFinding, setonFinding] = useState(false);
    const [onCodeing, setonCodeing] = useState(false);
    const [codeTime, setcodeTime] = useState(60);
    const [codeID, setcodeID] = useState('');

    let { t, i18n } = useTranslation()

    const [findPass, { loading: loadindA, error: errorA, data: dataA }] = useMutation(FINDPASS, {
        fetchPolicy: "no-cache"
    });
    const [sendSMS, { loading: loadindB, error: errorB, data: dataB }] = useMutation(SENDSMS, {
        fetchPolicy: "no-cache"
    });
    const [checkUser, { loading: loadindC, error: errorC, data: dataC }] = useLazyQuery(CHECKUSER, {
        fetchPolicy: "no-cache"
    });

    //输入内容
    const submitPhone = (v) => {
        let phoneNum = Number(v.replace(/\s*/g, ""));
        setphone(phoneNum);
    }
    const submitFirstPsw = (v) => {
        setfirstPsw(v);
        if (v !== secondPsw) {
            sethasError(true);
        }
        else {
            sethasError(false);
        }
    }
    const submitSecondPsw = (v) => {
        setsecondPsw(v);
        if (v !== firstPsw) {
            sethasError(true);
        }
        else {
            sethasError(false);
        }
    }
    const submitCode = (v) => {
        setcode(v);
    }
    //错误检测
    const onErrorClick = () => {
        if (hasError) {
            Toast.info(t('pswCheck'), 1);
        }
    }


    //提交找密
    const submitResult = () => {
        if (!onFinding) {
            let myreg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/;
            let result = {
                phone: '+86' + phone.toString(),
                password: firstPsw,
                code: code,
            };
            // console.log(result);
            if (firstPsw === '' && secondPsw === '') {
                Toast.info(t('enterNew'), 1);
            }
            else if (hasError) {
                Toast.info(t('pswCheck'), 1);
            }
            else if (!myreg.test(phone)) {
                Toast.info(t('enterPhoneNum'), 1)
            }
            else if (codeID === '') {
                Toast.info(t('verifyCodeErrRetry'), 1)
            }
            else {
                findPass({
                    variables: {
                        findPassInput: {
                            "password": result.password,
                            "phone": result.phone,
                            "verifyCodeId": codeID,
                            "verifyCode": result.code
                        }
                    }
                });
            }
        }
        else{
            Toast.info(t('changeWait'), 1)
        }
    }

    // 发送验证
    const submitVerify = () => {
        if (!onCodeing) {
            let myreg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/;
            if (myreg.test(phone)) {
                checkUser({
                    variables: {
                        "phone": '+86' + phone,
                    }
                })
                setonCodeing(true);
            }
            else {
                Toast.info(t('enterPhoneNum'), 1);
            }
        }
        else {
            Toast.info(t('codeWait'), 1);
        }

    }
    //验证码间隔倒计时
    useEffect(() => {
        if (whenSendCode) {
            let time = 60;
            var aa = setInterval(function () {
                if (time > 0) {
                    time -= 1;
                    setcodeTime(time);
                }
                else {
                    setcodeTime(60);
                    setwhenSendCode(false)
                    clearInterval(aa)
                }
            }, 1000);
        }
    }, [whenSendCode]);

    //改密请求
    useEffect(() => {
        if (dataA) {
            setonFinding(false);
            prop.history.push({ pathname: '/pswResult' });
        }
        if (loadindA) {
            setonFinding(true);
            console.log(t('changing'));
        }
        if (errorA) {
            setonFinding(false);
            if (errorA.graphQLErrors.length > 0) {

                if (errorA.graphQLErrors[0].extensions) {
                    let res = errorA.graphQLErrors[0].extensions.response;
                    console.log(res);
                    for (let i = 0; i < res.length; i++) {
                        if (res[i].search('not exist') > -1) {
                            Toast.info(t('checkPhoneNo'), 1.5);
                        }
                        else if (res[i].search('verifyCode') > -1) {
                            Toast.info(t('verifyCodeErr'), 1.5);
                        }
                        else {
                            Toast.info(t('verifyCodeErrRetry'), 1.5);
                        }
                    }
                }
                else {
                    Toast.info(t('netError'), 2);
                    console.log(errorA.graphQLErrors);
                }
            }
            else {
                Toast.info(t('netError'), 2);
                console.log(errorA.graphQLErrors);
            }
        }
    }, [loadindA, errorA, dataA]);

    //验证码请求
    useEffect(() => {
        if (dataB) {
            Toast.info(t('sendOver'), 1);
            setcodeID(dataB.createSMS.id);
        }
        if (loadindB) {
            // console.log('验证码发送中');
        }
        if (errorB) {
            if (errorB.graphQLErrors) {
                if (errorB.graphQLErrors[0].extensions) {
                    let res = errorB.graphQLErrors[0].extensions.response;
                    console.log(res);
                    for (let i = 0; i < res.length; i++) {
                        if (res[i].search('not exist') > -1) {
                            Toast.info(t('checkPhoneNo'), 1.5);
                        }
                        else {
                            Toast.info(t('netError'), 1.5);
                        }
                    }
                }
                else {
                    Toast.info(t('netError'), 1.5);
                    console.log(errorB.graphQLErrors);
                }
            }
            else {
                Toast.info(t('netError'), 1.5);
                console.log(errorB);
            }
        }
    }, [loadindB, errorB, dataB]);

    //账户验证请求
    useEffect(() => {
        if (dataC) {
            setonCodeing(false);
            if (dataC.userIsExist) {
                if (dataC.userIsExist.phone) {
                    setwhenSendCode(true);
                    sendSMS({
                        variables: {
                            createSMSInput: {
                                "phone": '+86' + phone
                            }
                        }
                    });
                }
                else Toast.info(t('checkPhoneNo'), 2);


            }
            else {
                Toast.info(t('checkPhone'), 2);
            }

        }
        if (loadindC) {
            // console.log('检查账号中');
        }
        if (errorC) {
            console.log(errorC);
            setonCodeing(false);
            Toast.info(t('netError'), 2);
        }
    }, [loadindC, errorC, dataC]);

    document.onkeydown = function (event) {
        var e = event || window.event;
        //  || arguments.callee.caller.arguments[0];
        if (e && e.keyCode === 13) {
            submitResult();
        }
    };

    return (

        <div>
            <p style={{ fontSize: '30px', margin: '4vh 0 0 5vw' }}>{t('findPsw')}</p>

            <WingBlank size="lg">

                <List style={{ border: '2px solid #434343', margin: '5vh 0 0 0' }}>
                    <InputItem
                        type={'phone'}
                        placeholder={t('phone')}
                        clear
                        onChange={submitPhone}
                        moneyKeyboardAlign="left"
                    // moneyKeyboardWrapProps={moneyKeyboardWrapProps}
                    ></InputItem>
                </List>

                <List style={{ border: '2px solid #434343', margin: '5vh 0 0 0' }}>
                    <InputItem
                        type={'password'}
                        placeholder={t('newPsw')}
                        clear
                        onChange={submitFirstPsw}
                        moneyKeyboardAlign="left"
                    // moneyKeyboardWrapProps={moneyKeyboardWrapProps}
                    ></InputItem>
                </List>

                <List style={{ border: '2px solid #434343', margin: '5vh 0' }}>
                    <InputItem
                        type={'password'}
                        placeholder={t('checkPSW')}
                        clear
                        onChange={submitSecondPsw}
                        moneyKeyboardAlign="left"
                        error={hasError}
                        onErrorClick={onErrorClick}
                    // moneyKeyboardWrapProps={moneyKeyboardWrapProps}
                    ></InputItem>
                </List>

                <Flex>
                    <Flex.Item>
                        <List style={{ border: '2px solid #434343', width: '45vw', float: 'left' }}>
                            <InputItem
                                type={'text'}
                                placeholder={t('verifyCode')}
                                clear
                                onChange={submitCode}
                                moneyKeyboardAlign="left"
                            // moneyKeyboardWrapProps={moneyKeyboardWrapProps}
                            ></InputItem>

                        </List>
                    </Flex.Item>
                    <Flex.Item>
                        <Button type="primary"
                            onClick={submitVerify}
                            style={{ width: '45vw', float: 'right', backgroundColor: '#434343', border: '1px solid #434343', color: '#ffffff' }}
                            disabled={whenSendCode}
                            loading={onCodeing}
                        >{t('sendCode')}{whenSendCode ? codeTime + 's' : ''}</Button>
                    </Flex.Item>
                </Flex>

                <WhiteSpace size="lg" />

            </WingBlank>

            <WingBlank size="lg">
                <Button type="primary" onClick={submitResult} style={{ marginTop: '10vh', backgroundColor: '#434343', border: '1px solid #434343', color: '#ffffff' }}>{t('ok')}</Button>
            </WingBlank>
        </div>

    );

}